import React from 'react'

import { VOLTAGE_OPTIONS, TARIFF_OPTIONS, FIELDS } from '../../../../constants/voltage'
import { TransferPriceTableType } from '../../../../prop-types/voltageProps'

import { Container } from './Container'
import styles from './controls.module.less'

export const VoltageLevels = ({ transferPriceTable }) => {
  const [voltageValue, tariffValue] = Object.entries(transferPriceTable)[0] || []
  const voltageValueLabel = VOLTAGE_OPTIONS.find(({ value }) => value === voltageValue)?.label
  const tariffValueLabel = TARIFF_OPTIONS.find(({ value }) => value === tariffValue)?.label

  return (
    <Container>
      <span className={styles.title}>Уровни напряжения</span>
      <div className={styles.values}>
        <label className={styles.label}>{FIELDS.voltage.label}</label>
        <span className={styles.value}>{voltageValueLabel}</span>
        <label className={styles.label}>{FIELDS.tariff.label}</label>
        <span className={styles.value}>{tariffValueLabel}</span>
      </div>
    </Container>
  )
}

VoltageLevels.propTypes = TransferPriceTableType.isRequired

VoltageLevels.defaultProps = {
  transferPriceTable: {},
}
